@import url("https://fonts.googleapis.com/css2?family=Outfit:wght@100..900&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Caprasimo&:wght@500&display=swap");
/* styles/globals.css */
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}
body {
  font-family: "Outfit", sans-serif;
  color: #333;

  display: block;
  width: auto;
  max-width: 1200px;

  margin: 0 auto;
  padding: 0 40px;
}

a {
  text-decoration: none;
}

a:hover {
  text-decoration: underline;
}

h1 {
  font-size: 12em;
  color: orangered;
}

ul {
  list-style: none;
}
